<template lang="pug">
  van-form(@submit="saveConfig")
    van-cell-group(inset title="用户标识")
      van-field(v-model="userIds" :rules="rules.userIds" label="用户ID" placeholder="请输入用户ID(多个用,分割)")
    van-button(type="info" style="width: calc(100% - 32px); margin: 10px 16px 0 16px;" native-type="submit") 保存
</template>

<script>
export default {
  data() {
    return {
      userIds: '',
      rules: {
        userIds: [{
          validator: () => {
            const userIdArr = this.str2Arr(this.userIds)
            const hasNaN = userIdArr.find((userId) => isNaN(userId))
            return !hasNaN
          },
          message: '用户ID格式不正确'
        }]
      }
    }
  },
  created() {
    try {
      const userIds = JSON.parse(localStorage.getItem('config')).userIds
      this.userIds = userIds.join(',')
    } catch {
      console.log('未配置用户')
    }
  },
  methods: {
    str2Arr(str) {
      return Array.from(new Set(str
        .replaceAll('，', ',')
        .split(',')
        .map(item => item.trim())
        .filter(item => item !== '')))
    },
    // 保存配置
    saveConfig() {
      localStorage.setItem('config', JSON.stringify({
        // 用户ID，用逗号分割线，去掉头尾空格，过滤掉空字符串
        userIds: this.str2Arr(this.userIds)
      }))
      this.$toast.success('保存成功')
    }
  }
}
</script>
